.stats{
  background-image: url(../../assets/images/grass.jpg);
  background-size: contain;
  height: 100%;
  padding-bottom: 1rem;

  &__chart-cont{
    margin: 0 1rem;
  }

  &__chart{
    background-color: white;
    border-radius: 5px;
    opacity: 90%;
    margin: 0 auto 2rem auto;
    box-shadow: 10px 10px 20px 1px black;
    max-width: 700px;
  }

  &__title{
    color: white;
    background-color: #329ca8;
    padding: 1rem 1rem;
    margin-bottom: 1rem;
    height: 4rem;
  }
}